.more-info {
    width: 100%;
    background-color: black;
    padding-top: 2%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    color: white;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.more-info-pic {
    border-radius: 50%;
}

.x {
    position: absolute;
    right: 4%;
    top: 3%;
    cursor: pointer;
}

.page-link {
    background: black !important;
    color: white !important
}

.active > .page-link {
    background-color: #007bff !important;
}

.episodes {
    width: 250px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.episode {
    width: 30px;
    margin: 5px;
    padding: 2px;
    border: 1px solid white;
    border-radius: 50%;
    transition: 0.4s ease;
    cursor: default;
    text-align: center;
    
}

.episode:hover {
    background-color: white;
    color: black
}