.wrapper-main {
    width: 100%;
    overflow: hidden;
    padding-top: 5%;
    display: flex;
    justify-content: center;
}

.characters-main,
.locations-main,
.episodes-main {
    width: 200px;
    cursor: pointer;
    border: 1px solid white;
    border-radius: 20px;
    margin: 1%;
    padding: 1%;
    transition: 0.4s;
}

.characters-main:hover,
.locations-main:hover,
.episodes-main:hover {
    background-color: white;
    color: black;
    cursor: pointer;
}

.card-img-top {
    height: 85%;
    /* width: 100%; */
}