.episodes-div {
    width: 100%;
    text-align: center;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.episode-div {
    display: flex;
    justify-content: space-between;
    width: 60%;
    overflow: hidden;
    border-bottom: 1px solid white;
    cursor: pointer;
}

p {
    margin-bottom: 5px;
    margin-top: 5px;
}

.episode-div:hover {
    background-color: white;
    color: black;
}

.pagination {
    margin-top: 5%;
}

.characters-appeared {
    margin-top: 3%;
}

.cards-episodes {
    margin-top: 3%;
}