body {
  background-color: black;
  color: white;
}

@font-face {
  font-family: rickAndMorty;
  src: url(https://github.com/Adirdayan13/rick-and-mortypedia/getschwifty.ttf) format('ttf');
}

h1 {
  color: white;
}

.intro {
  display: grid;
  grid-template-columns: 120px 1fr;
  grid-template-rows: 150px;
}

.intro h1 {
  color:#28B0C9;
  text-align: center;
  font-family: rickAndMorty;
  font-size: 80px;
  /* -webkit-text-fill-color: white; Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #C9DC5A;
  grid-column: 2/3;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10%;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 1%;
}

.character-image {
  width: 120px;
}

.logo {
  width: 100px;
  margin-top: 10%;
  margin-left: 10%;
  grid-column: 1/2;
}

.cards {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.card {
  margin: 20px;
  /* width: 270px; */
  width: 250px;
  word-break: normal;
  background-color: black;
  color: white;
  border: 1px solid white;
}

.list-group-item {
  background: black;
  border: 1px solid rgba(255, 255, 255, 0.55);
  font-size: 1rem;
  transition: 0.4s ease;
  height: max-content;
}

.list-group-item:hover {
  background-color: white;
  color: black;
  cursor: default;
}