 .locations {
     text-align: center;
     align-items: center;
     margin-top: 2%;
    }
    
.all-locations {
    justify-content: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.locations h2 {
    margin-bottom: 5%;
}

.location {
    width: 200px;
    height: 200px;
    border: 1px solid white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 1%;
    transition: 0.4s;
    background-image: url(https://github.com/Adirdayan13/rick-and-mortypedia/pictures/world.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    /* color: black; */
    font-weight: bold;
    -webkit-text-fill-color: white; /* Will override color (regardless of order) */
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    font-size: 25px;
}

.location:hover {
    background-color: white;
    width: 220px;
    height: 220px;
    color: black;
    cursor: pointer;
}

.black {
  background-color: black;
}

.residents {
    display: flex;
    flex-direction: column;
}

.resident {
    display: flex;
    flex-direction: column-reverse;
}

.wrapper {
    width: 500px;
    border: 1px solid white;
    overflow: hidden;
    cursor: pointer;
}

.wrapper:hover {
    background-color: white;
    color: black;
}

.first {
    width: 250px;
    float: left;
}

.second {
    overflow: hidden;
}

.x-locations {
    position: absolute;
    right: 3%;
    top: 3%;
    cursor: pointer;
    color: white;
}

.card {
    text-align: center;
}

